<template>
  <div>
    <el-header class="header_bg">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <!--{{asideNavTitle.length!=0?asideNavTitle[0]:""}}-->
        <el-breadcrumb-item>订单管理</el-breadcrumb-item>
      </el-breadcrumb>
    </el-header>
    <div class="margin_top15">
      <el-tabs v-model="activeName" @tab-click="handleClick" class="white_bg_color padding_left_right">
        <el-tab-pane label="未付款" name="first">
          <div>
            <el-row class="text-center">
              <el-col :span="13">
                <el-row>
                  <el-col :span="10">
                    <p>商品</p>
                  </el-col>
                  <el-col :span="6">
                    <p>单价</p>
                  </el-col>
                  <el-col :span="6">
                    <p>数量</p>
                  </el-col>
                </el-row>
              </el-col>
              <el-col :span="3">
                <p>订单总额</p>
              </el-col>
              <el-col :span="3">
                <p>状态</p>
              </el-col>
              <el-col :span="5">
                <p>操作</p>
              </el-col>
            </el-row>
            <div class="div_border">
              <el-row class="gray_bg">
                <el-col :span="5">
                  <p class="margin_top5">订单号：{{orderNumber}}</p>
                </el-col>
                <el-col :span="3">
                  <p class="margin_top5">{{orderDate}}</p>
                </el-col>
              </el-row>
              <el-row class="text-center vertical-container">
                <el-col :span="13">
                  <el-col v-for="(order,index) in orderInfo" class="special_border" :key="index">
                    <el-col :span="10" class="margin_top5 margin_left5">
                      <div style="float: left">
                        <img src="../../assets/customerImg/58tc@2x.png" height="64" width="64"/>
                      </div>
                      <div>
                        <p class="order_title">{{order.goodsName}}</p>
                      </div>
                    </el-col>
                    <el-col :span="6" class="margin_top5">
                      <p class="order_title">¥{{order.orderPrice}}</p>
                    </el-col>
                    <el-col :span="6" class="margin_top5">
                      <p>x{{order.num}}</p>
                    </el-col>
                  </el-col>
                </el-col>
                <el-col :span="3" class="">
                  <!--<div class="vertical-container">-->
                  <p class="margin_top5 order_price vertical-container">¥{{orderSumPrice}}</p>
                  <p class="pay_method vertical-container">{{payMethods}}</p>
                  <!--</div>-->

                </el-col>
                <el-col :span="3" class="">
                  <p class="margin_top5 unPay_btn">{{orderState=== 1 ?'已支付':'待支付'}}</p>
                  <el-button type="text" v-if="orderState !== 0">查看详细</el-button>

                </el-col>
                <el-col :span="5" class="">
                  <el-button type="text">取消</el-button>
                  <el-button type="btn" size="small" class="pay_btn">去支付</el-button>
                </el-col>
              </el-row>
            </div>
            <div class="block" v-if="total>0">
              <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page.sync="currentPage"
                :page-size="10"
                layout="total, prev, pager, next"
                :total=total>
              </el-pagination>
            </div>
          </div>
        </el-tab-pane>
        <el-tab-pane label="已完成" name="second">已完成</el-tab-pane>
        <el-tab-pane label="全部" name="third">全部</el-tab-pane>
      </el-tabs>


    </div>

  </div>
</template>

<script>
  // import tableDate from '../../../mock/tableData2.js'
  import {mapGetters} from 'vuex'
  import swal from 'sweetalert2'; //alert框
  import VerifyUtils from '../../utils/utils';

  export default {
    name: 'OrderManage',
    // props: ['selectTitle'],
    data() {
      return {
        activeName: 'first',
        orderNumber: '12434343',
        orderDate: '2018-09-12',
        orderInfo: [{
          goodsName: '入职体检A套餐',
          medicalInstitution: '慈铭体检',
          num: 1,//订单数量
          orderPrice: '128',
          // payMethods:'在线支付',
        }, {
          goodsName: '入职体检A套餐',
          num: 1,//订单数量
          orderPrice: '128',
          // payMethods:'在线支付',
        },],
        orderSumPrice: '128',
        payMethods: '在线支付',
        orderState: 0,//订单状态
        tableData: [],
        currentPage: 1,
        total: 1,
        tableData6: [{
          id: '12987122',
          name: '王小虎',
          amount1: '234',
          amount2: '3.2',
          amount3: 10
        }, {
          id: '12987123',
          name: '王小虎',
          amount1: '165',
          amount2: '4.43',
          amount3: 12
        }, {
          id: '12987124',
          name: '王小虎',
          amount1: '324',
          amount2: '1.9',
          amount3: 9
        }, {
          id: '12987125',
          name: '王小虎',
          amount1: '621',
          amount2: '2.2',
          amount3: 17
        }, {
          id: '12987126',
          name: '王小虎',
          amount1: '539',
          amount2: '4.1',
          amount3: 15
        }]
      }
    },
    computed: {
      ...mapGetters(['asideNavTitle'])
    },
    created() {
      this.getFirmListData();
    },
    methods: {
      getFirmListData() {
        let _self = this
        //获取企业列表
        _self.$get('/user/enterprise/list', {
          page: this.currentPage,
          perPage: '10',
          optName: '',
          optState: '',
          optUser: '',
          optCounty: '',
          optEnterpriseName: ''
        }).then(res => {
          if (JSON.stringify(res.data.code) == 0) {
            console.log("企业列表" + JSON.stringify(res.data.data.items));
            _self.tableData = res.data.data.items;
            _self.total = res.data.data.total
            _self.currentPage = res.data.data.page
          } else {
            alert(JSON.stringify(res.data.message))
          }
        }).catch(function (reason) {
          console.log('catch:', reason);
        });
      },
      clear() {

      },
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
        this.currentPage = val;
        this.getFirmListData();
      },
      handleClick(tab, event) {
        console.log(tab, event);
      },
      cancelOperate(rowData) {
        swal({
          title: "是否确定取消?",
          text: "",
          type: "warning",
          showCancelButton: true,
          confirmButtonColor: "#DD6B55",
          confirmButtonText: "确定",
          cancelButtonText: "取消"
        }).then((result) => {
          if (result.value) {
            this.$post('/user/user/del', {
              enterpriseId: rowData.enterpriseId
            }).then((res) => {
              // alert(JSON.stringify(res.data.message))
              if (JSON.stringify(res.data.code) == 0) {
                this.getFirmListData()
                swal("Deleted!", "这条数据已被删除", "success");
              } else {
                alert(JSON.stringify(res.data.message))
              }
            })
          } else {
            swal("Cancelled", "这条数据还在哦", "error");
          }
        });
      },

    }
  }
</script>

<style>
  @import "../../styles/personal/personal-center.scss";

  .header_bg {
    background-color: white;
    text-align: right;
    font-size: 18px;
    color: #333333;
  }

  .el-breadcrumb {
    line-height: 3.5;
    font-size: 18px;
  }

  .marginTop10 {
    margin-top: 0.6rem;
  }

  .gray_bg {
    background-color: #eeeeee;
  }

  .margin_top5 {
    margin-top: 0.65rem;
  }

  .margin_left5 {
    margin-left: 5px;
  }

  .div_border {
    border: 1px solid #eeeeee;
  }

  .special_border {
    border-top: 1px solid #eeeeee;
    border-right: 1px solid #eeeeee;
  }

  .special_top_border {
    border-top: 1px solid #eeeeee;
  }

  .order_title {
    color: #404040;
    font-size: 14px;
  }

  .unPay_btn {
    color: #5BC146;
    font-size: 14px;
  }

  .order_price {
    font-size: 14px;
    color: #404040;
  }

  .pay_method {
    color: #666666;
    font-size: 14px;
  }

  .pay_btn {
    /*width:42px;*/
    /*height:14px;*/
    font-size: 14px;
    font-weight: normal;
    color: rgba(255, 255, 255, 1);
    background-color: #DF2321;
  }

  .vertical-container {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
    align-items: center;
    -webkit-justify-content: center;
    justify-content: center;
  }

  .redColor {
    color: red;
  }
  .padding_left_right{
    padding-left: 10px;
    padding-right: 10px;
  }

</style>



