<template>
  <div class="margin_left_right white_bg_color">
    <div>
      <hr class="hr_color">
      <el-row>
        <el-col :span="4">
          <div class="suit_border"></div>
        </el-col>
        <el-col :span="18" class="text_align_left padding_left10">
          <p class="sub_font">由<span class="special_font">{{medicalCenter}}</span>提供</p>
          <p class="title_font">{{productName}}</p>
          <el-row>
            <el-col :span="2">
              <p class="sub_font">适用人群</p>
            </el-col>
            <el-col :span="18">
              <p class="regular_font ">{{suitSex===0?'男性、女性':(suitSex===1?'男性':'女性')}}</p>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="2">
              <p class="sub_font">套餐特点</p>
            </el-col>
            <el-col :span="18">
              <p class="regular_font">{{packageDescription}}</p>
            </el-col>
          </el-row>
          <p class="margin_top0_bottom0"><span class="discount_price">¥</span><span
            class="discount_price">{{salePrice}}</span><span class="origin_price">起</span>&emsp;&emsp;&emsp;<span
            class="origin_price line">¥</span><span class="origin_price line">{{marketPrice}}</span></p>
          <el-row>
            <el-col :span="2">
              <p class="sub_font">数量</p>
            </el-col>
            <el-col :span="18">
              <el-input-number v-model="shoppingCart" @change="cartHandleChange" :min="1" :max="100"
                               label="描述文字"></el-input-number>
            </el-col>
          </el-row>
          <el-row class="margin_top15">
            <el-col :span="6">
              <el-button size="medium" class="default_font" :class="{clickFont:active}" @click="addCart()">加入购物车
              </el-button>
            </el-col>
            <el-col :span="12">
              <el-button size="medium" class="default_font" :class="{clickFont:active}">立即购买</el-button>
            </el-col>
          </el-row>
        </el-col>
      </el-row>
    </div>
    <div class="margin_top15">
      <el-row :gutter="25">
        <el-col :span="19">
          <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
            <el-tab-pane label="套餐详细" name="first">
              <div class="text_align_left padding_left10">
                <p class="second_font">入职体检</p>
                <el-row>
                  <el-col :span="2">
                    <p class="sub_font">适用人群</p>
                  </el-col>
                  <el-col :span="18">
                    <p class="regular_font ">{{suitSex===0?'男性、女性':(suitSex===1?'男性':'女性')}}</p>
                  </el-col>
                </el-row>
                <el-row>
                  <el-col :span="2">
                    <p class="sub_font">套餐特点</p>
                  </el-col>
                  <el-col :span="18">
                    <p class="regular_font">{{packageDescription}}</p>
                  </el-col>
                </el-row>
                <p class="second_font">入职体检</p>
                <div class="">
                  <el-table
                    :data="packageItems"
                    tooltip-effect="dark"
                    style="width: 100%"
                    :header-cell-style="tableHeaderColor" class="padding_left10">
                    <el-table-column
                      prop="id"
                      label="ID">
                    </el-table-column>
                    <el-table-column
                      prop="name"
                      label="基础检查项目">
                    </el-table-column>
                    <el-table-column
                      prop="description"
                      label="检查目的">
                    </el-table-column>
                  </el-table>
                </div>

              </div>

            </el-tab-pane>
            <el-tab-pane label="机构介绍" name="second">
              <div>
                <div class="store_info">
                  <el-row>
                    <el-col :span="6" class="padding_left10">
                      <div class="institution_img"></div>
                    </el-col>
                    <el-col :span="18" class="text_align_left">
                      <div>
                        <p class="stores_name">{{storesName}}</p>
                        <p class="regular_font">
                          <img class="icon_img" src="../../assets/medicalproduct/didian_tjcp_jgjs@2x.png" height="18"
                               width="18"/>
                          <span>{{storesAddress}}</span>
                        </p>
                        <p class="regular_font">
                          <img class="icon_img" src="../../assets/medicalproduct/dianhua_tjcp_jgjs@2x.png" height="18"
                               width="18"/>
                          {{storePhone}}</p>
                        <p class="regular_font">
                          <img class="icon_img" src="../../assets/medicalproduct/shijian_tjcp_jgjs@2x.png" height="18"
                               width="18"/>
                          {{businessHours}}
                        </p>
                      </div>
                    </el-col>
                  </el-row>
                </div>
                <div class="text_align_left padding_left10">
                  <p class="stores_name">机构简介</p>
                  <p class="stores_introduce">
                   {{description}}
                  </p>
                  <!--<p class="stores_introduce">-->
                    <!--泉州910医院体检中心拥有一栋独立的体检大楼，营业面积达6000平方米，环境宽敞，按照五星级健康休闲会所标准建造，全程数字化管理，医检分离;可为客户提供全程导检等一站式服务。中心分设贵宾区体检部、普检区体检部，实行男、女分检;体检资料私密化管理，多形式导诊，多窗口、大流量快捷优质服务。中心以“品质第一，客户至上”的服务理念，并为客户量身订制近百种个性化体检套餐，为体检异常者，提供专业复检方案;根据客户要求提供各种健康管理服务。-->
                  <!--</p>-->
                </div>
                <div class="text_align_left padding_left10">
                  <p class="stores_name">机构位置</p>
                  <p class="stores_introduce">{{institutionLocate}}</p>
                  <div>
                    <img src="../../assets/medicalproduct/ditu_img@2x.png" height="402"/></div>
                </div>
              </div>
            </el-tab-pane>
            <el-tab-pane label="更多机构" name="third">
              <div>
                <div class="store_info">
                  <el-row>
                    <el-col :span="6" class="padding_left10">
                      <div class="institution_img"></div>
                    </el-col>
                    <el-col :span="18" class="text_align_left">
                      <div>
                        <p class="stores_name">{{storesName}}<span><u class="special_font select_institution">选择该机构</u></span>
                        </p>
                        <p class="regular_font">
                          <img class="icon_img" src="../../assets/medicalproduct/didian_tjcp_jgjs@2x.png" height="18"
                               width="18"/>
                          <span>{{storesAddress}}</span>
                        </p>
                        <p class="regular_font">
                          <img class="icon_img" src="../../assets/medicalproduct/dianhua_tjcp_jgjs@2x.png" height="18"
                               width="18"/>
                          {{storePhone}}</p>
                        <p class="regular_font">
                          <img class="icon_img" src="../../assets/medicalproduct/shijian_tjcp_jgjs@2x.png" height="18"
                               width="18"/>
                          {{businessHours}}
                        </p>
                      </div>
                    </el-col>
                  </el-row>
                </div>
              </div>
            </el-tab-pane>
          </el-tabs>
        </el-col>
        <el-col :span="5">
          <div class="float_left regular_font">
            推荐套餐
          </div>
          <el-row>
            <el-col :span="20" v-for="(o, index) in 2" :key="o">
              <el-card :body-style="{ padding: '0px' }">
                <img src="../../assets/checkUp/taocan_img1@2x.png" class="image">
                <div class="card_content card_align">
                  <p class="card_title">亲友关怀套餐(男士)</p>
                  <span class="card_subtitle">颈腰椎劳损、肿瘤筛查</span>
                  <p class="margin_top0_bottom0"><span class="discount_price">¥</span><span
                    class="discount_price">558</span><span class="origin_price">起</span>&emsp;&emsp;&emsp;<span
                    class="origin_price line">¥</span><span class="origin_price line">892</span></p>
                </div>
              </el-card>
            </el-col>
          </el-row>
        </el-col>
      </el-row>

    </div>
  </div>
   
</template>
<script>
  export default {
    data() {
      return {
        medicalCenter: '',//体检中心
        productName: '',
        suitSex: 0,//1男 2女 0男女
        salePrice: '',
        marketPrice: '0',
        packageDescription: '',//套餐描述
        institutionId: '',//机构id
        packageItems:[],//体检项目
        shoppingCart: 1,
        active: false,
        activeName: 'first',
        tableData: [
          {
            number: 1,
            medicalProduct: '身高、体重、体重指数、血压、腰围、等。',
            medicalAim: '提供准确的早期预警，让您更好的了解目前身体状况，及早发现潜在的危害健康。'
          },
          {
            number: 2,
            medicalProduct: '身高、体重、体重指数、血压、腰围、等。',
            medicalAim: '提供准确的早期预警，让您更好的了解目前身体状况，及早发现潜在的危害健康。'
          },
          {
            number: 3,
            medicalProduct: '身高、体重、体重指数、血压、腰围、等。',
            medicalAim: '提供准确的早期预警，让您更好的了解目前身体状况，及早发现潜在的危害健康。'
          },
          {
            number: 4,
            medicalProduct: '身高、体重、体重指数、血压、腰围、等。',
            medicalAim: '提供准确的早期预警，让您更好的了解目前身体状况，及早发现潜在的危害健康。'
          },

        ],
        storesName: '慈铭体检中心',
        storesAddress: '',
        storePhone: '4001-1211-00',
        businessHours: '',
        description:'XXXX',//机构简介
        institutionLocate: '地址：福建泉州市丰泽区清源山下第910医院',
      }
    },
    created() {
      this.getDetailInfo();
      //获取更多体检机构
      // this.getMoreInstitution();
    },
    methods: {
      getDetailInfo() {
        let url = '/api/user/product/detail/' + this.$route.query.productId;
        this.$get(url, {}).then(res => {
          if (JSON.stringify(res.data.code) == 200) {
            console.log("detail==" + JSON.stringify(res.data));
            let productDetail = res.data.data.productDetail
            this.medicalCenter = productDetail.centerName;
            this.productName = productDetail.productName;
            this.salePrice = productDetail.salePrice;
            this.marketPrice = productDetail.marketPrice;
            this.packageDescription = productDetail.packageDescription;
            this.institutionId = productDetail.centerId;
            this.packageItems = productDetail.packageItems;
            //获取机构介绍
            this.getInstitutionRelate();
          } else {
            alert(res.data.msg);
          }
        }).catch(function (reason) {
          console.log('catch:', reason);
        });
      },
      //产品详情页-获取套餐对应的机构信息
      getInstitutionRelate() {
        let url = '/api/user/product/institutionDetail/' + this.institutionId;
        this.$get(url, {}).then(res => {
          if (JSON.stringify(res.data.code) == 200) {
            console.log("机构==" + JSON.stringify(res.data));
            let institutionsDetail = res.data.data;
            this.storesName = institutionsDetail.name;
            this.businessHours = institutionsDetail.businessHours;
            this.storePhone = institutionsDetail.telephone;
            this.description = institutionsDetail.description;
            this.storesAddress = institutionsDetail.province +institutionsDetail.city;
            this.institutionLocate = institutionsDetail.address;
          } else {
            alert(res.data.msg);
          }
        }).catch(function (reason) {
          console.log('catch:', reason);
        });
      },
      getMoreInstitution() {
        let url = '/api/user/product/institutions/' + this.$route.query.productId;
        this.$get(url, {}).then(res => {
          debugger
          if (JSON.stringify(res.data.code) == 200) {
            console.log("更多机构==" + JSON.stringify(res.data));
          } else {
            alert(res.data.msg);
          }
        }).catch(function (reason) {
          console.log('catch:', reason);
        });
      },
      cartHandleChange(value) {
        console.log(value);
      },
      addCart() {
        this.active = true;
        let url = '/api/user/shopCar/products/' + localStorage.getItem('userId');
        this.$get(url, {}).then(res => {
          debugger
          if (JSON.stringify(res.data.code) == 200) {
            console.log("cart==" + JSON.stringify(res.data));
          } else {
            alert(res.data.msg);
          }
        }).catch(function (reason) {
          console.log('catch:', reason);
        });
      },
      handleClick(tab, event) {
        console.log(tab, event);
      },
      // 修改table header的背景色
      tableHeaderColor({row, column, rowIndex, columnIndex}) {
        if (rowIndex === 0) {
          return 'background-color: #F6F6F6;color: #404040;font-weight: 500;'
        }
      }
    }
  }
</script>
<style>
  @import "../../styles/personal/personal-center.scss";

  .margin_left_right {
    margin-left: 10%;
    margin-right: 10%;
  }

  .hr_color {
    height: 2px;
    background-color: #e6e6e6;
    border: none;
  }

  .suit_border {
    margin-top: 10px;
    border: 1px solid #e6e6e6;
    background-color: #EEEEEE;
    width: 190px;
    height: 190px;
  }

  .title_font {
    color: #333333;
    font-size: 18px;
    margin-top: 8px;
    margin-bottom: 8px;
  }

  .special_font {
    color: #009BDD;
    font-size: 14px;
  }

  .sub_font {
    color: #a2a2a2;
    font-size: 14px;
    margin-top: 6px;
    margin-bottom: 6px;
  }

  .second_font {
    color: #666666;
    font-size: 16px;
    margin-top: 6px;
    margin-bottom: 6px;
  }

  .regular_font {
    color: #666666;
    font-size: 14px;
    margin-top: 6px;
    margin-bottom: 6px;
  }

  .default_font {
    background-color: #FFECED;
    color: #DF2321;
    border: 1px solid #DF2321;
  }

  .clickFont {
    background-color: #DF2321;
    color: #ffffff;
  }

  .tab_bg_color {
    border: 1px solid rgba(238, 238, 238, 1);
    background-color: #EEEEEE;
  }

  /*机构介绍的样式*/
  .institution_img {
    margin-top: 10px;
    margin-bottom: 10px;
    border: 1px solid #e6e6e6;
    background-color: #EEEEEE;
    width: 200px;
    height: 150px;
  }

  .store_info {
    border: 1px solid rgba(238, 238, 238, 1);
  }

  .icon_img {
    vertical-align: middle;
  }

  .stores_name {
    font-size: 16px;
    color: #404040;
  }

  .stores_introduce {
    font-size: 12px;
    color: #666666;
  }

  .select_institution {
    float: right;
    padding-right: 10px;
  }
</style>





