<template>
  <el-container>
    <div class="banner">
      <el-row :gutter="10">
        <el-col :span="4" class="text_align_right margin_top5">
          <span>神州四达旗下品牌</span>
        </el-col>
        <el-col :span="8">
          <div class="text_align_left margin_top5">
            <span>{{currentCity}}</span>
            <span>|</span>
            <el-popover
              placement="bottom"
              width="400"
              trigger="click">
              <div style="height: 200px;overflow-y:auto">
                <ul class="ul_name">
                  <li v-for="(value,key,index) in city" :key="key" class="li_name" :class="activeClass == index ? 'active':''">
                    <a v-text="value" @click="chooseCity(value,key,index)"></a>
                  </li>
                </ul>
              </div>
              <span class="el-dropdown-link" slot="reference">
            更多城市<i class="el-icon-arrow-down el-icon--right"></i>
          </span>
            </el-popover>
            <!--下拉框-->
            <!--<el-dropdown>-->
          <!--<span class="el-dropdown-link">-->
            <!--更多城市<i class="el-icon-arrow-down el-icon&#45;&#45;right"></i>-->
          <!--</span>-->
              <!--<el-dropdown-menu slot="dropdown" style="height: 200px;overflow-y:auto">-->
                <!--<el-dropdown-item v-for="(value,key) in city" :key="key" @click="chooseCity(value,key)" :command='key'>-->
                  <!--{{value}}-->
                <!--</el-dropdown-item>-->
              <!--</el-dropdown-menu>-->
            <!--</el-dropdown>-->
          </div>
        </el-col>
        <el-col :span="6">
          <div class="text_align_right margin_top5">
            <span>您好，欢迎您</span>
            <span v-if="loginStatus !=1">
              <router-link :to="{path:'/'}" active-class="active" class="login_btn">
                登录
              </router-link>
              <span>或</span>
              <router-link :to="{path:'/'}" active-class="active" class="login_btn">
                免费注册
              </router-link>
            </span>
            <span v-else="loginStatus==1">
              <router-link :to="{path:'/Login'}" active-class="active" class="login_btn">
                退出登录
              </router-link>
            </span>

          </div>
        </el-col>
        <el-col :span="2">
          <div class="text_align_right margin_top5">
            我的订单
          </div>
        </el-col>
        <el-col :span="1">
          <div @click="cart()">
            <el-popover
              placement="bottom"
              width="350"
              trigger="hover">
              <div style="overflow-y: auto;height: 250px">
                <div v-for="(o, index) in 4" :key="o">
                  <el-row>
                    <el-col :span="8">
                      <div style="width:80px;height: 80px;background-color: #F6F6F6"></div>
                    </el-col>
                    <el-col :span="8">
                      <p class="cart_order_title text_align_left">入职体套餐（男女)</p>
                    </el-col>
                    <el-col :span="8">
                      <p class="cart_price">¥{{price}}</p>
                      <el-button type="text" class="cart_price" @click="deleteGoods()">删除</el-button>
                    </el-col>
                  </el-row>
                </div>
                <el-row class="bottom_div">
                  <el-col :span="12">
                    <p>共{{goodsNum}}件商品，共计¥{{goodsAmount}}</p>
                  </el-col>
                  <el-col :span="12" class="text_align_right">
                    <el-button type="danger" class="cart_btn" @click="goCart()">查看购物车</el-button>
                  </el-col>
                </el-row>

              </div>
              <div>

              </div>
              <el-button slot="reference" type="text" class="cart_link" style="">购物车</el-button>
            </el-popover>
            <!--<router-link :to="{path:'/ShoppingCart'}" active-class="active" class="login_btn" @click="cart()">-->

            <!--</router-link>-->
            <!--<el-button type="text" @click="cart()">购物车</el-button>-->
          </div>
        </el-col>
        <el-col :span="2">
          <div class="text_align_left margin_top5" @click="judgeParam()">
            <router-link v-if="(enterpriseId === 'undefined') ||  (enterpriseId === null)"
                         :to="{path:'/PersonalCenter',query: {name: 'person'}}"
                         active-class="active" class="personal_center">
              个人中心
            </router-link>
            <router-link v-if="(userId === 'undefined') || (userId === null)"
                         :to="{path:'/FirmCenter',query: {name: 'firm'}}"
                         active-class="active" class="personal_center">
              企业中心
            </router-link>

          </div>
        </el-col>
      </el-row>
    </div>
  </el-container>
</template>

<script>
  import {mapGetters} from 'vuex'

  export default {
    name: "Banner",
    data() {
      return {
        currentCity:'北京市',
        loginStatus: '0',
        userId: '',
        enterpriseId: '',
        price: '180',
        goodsNum: '2',
        goodsAmount: '256.00',
        city: '',
        activeClass: 0,
      }
    },
    created() {
      this.loginStatus = localStorage.getItem("loginStatus");
      this.enterpriseId = localStorage.getItem("enterpriseId");
      this.userId = localStorage.getItem("userId");
      this.getCityData();
      this.shopCartList();
      console.log(' this.loginStatus==' + this.loginStatus + "enterpriseId==" + this.enterpriseId + "userId==" + this.userId)
    },
    computed: {
      ...mapGetters(['cartParams'])
    },
    methods: {
      shopCartList() {
        let userId = localStorage.getItem("userId");
        let url = '/api/user/shopCar/products/'+ userId;
        this.$get(url, {}).then(res => {
          if (JSON.stringify(res.data.code) == 200) {
            console.log("cart==" + JSON.stringify(res.data.data));
          } else {
            alert(res.data.msg);
          }
        }).catch(function (reason) {
          console.log('catch:', reason);
        });
        // this.$router.push({path: '/ShoppingCart'});
        // this.$store.dispatch('setCartParams', 'cart');
        console.log('cartParams==' + this.cartParams);
      },
      deleteGoods(){

      },
      //跳转购物车页面
      goCart() {
        this.$router.push({path: '/ShoppingCart'});
        this.$store.dispatch('setCartParams', 'cart');
      },
      judgeParam() {
        // localStorage.setItem('center',para);
        this.$store.dispatch('setCartParams', 'center');
        console.log('cartParams11111==' + this.cartParams);
      },
      getCityData() {
        const config = require('../../static/region.json');
        this.city = config['86'];
        // console.log(JSON.stringify(this.city))
      },
      chooseCity(value,key,index) {
        this.currentCity = value;
        this.activeClass = index;
        console.log(key)
      }
    }
  }
</script>

<style scoped>
  @import "../styles/personal/personal-center.scss";

  .banner {
    width: 100%;
    height: 2rem;
    margin-top: 0px;
    background-color: rgba(246, 246, 246, 1);
    font-size: 0.81rem;
    border-bottom: 1px solid #f2f2f2;

  }

  .banner span, div {
    color: #6C6C6C;

  }

  .cart_link {
    margin-top: -5px;
    font-size: 0.81rem;
  }

  .cart_btn {
    font-size: 14px;
    font-color: #FFFFFF;
    background-color: #DF2321;
  }

  .el-dropdown {
    font-size: 0.81rem;
  }

  .margin_top5 {
    margin-top: 5px;
  }

  .text_align_left {
    text-align: left;
  }

  .text_align_right {
    text-align: right;
  }

  .login_btn {
    color: #33B2E8;
  }

  .personal_center {
    text-decoration: none;
  }

  .cart_order_title {
    font-size: 12px;
    color: #666666;
  }

  .cart_price {
    font-size: 14px;
    color: #404040;
  }

  .ul_name {
    display: inline;
    white-space: nowrap;
  }

  .li_name {
    list-style: none;
    display: inline-block;
    margin-left: 18px;
    float: left;
    line-height: 2;
  }
  .active {
    /* background: #eee; */
    color: #33B2E8;
    font-weight: bolder;
  }
</style>



