<template>
  <div>
    <div v-if="cartParams === 'cart' ">
      <!--<home-banner></home-banner>-->
      <home-header></home-header>
    </div>
    <!--<home-banner></home-banner>-->
    <!--<home-header></home-header>-->
    <div class="div_padding">
      <!--根据登录状态来判断是否显示-->
      <div class="un_login_prompt" v-if="loginStatus !=1">
        <el-row>
          <el-col :span="12" class="text_align_left padding_left10">
            <p class="prompt_font"><i class="el-icon-warning"></i>购物车内暂时没有商品，登录后将显示您之前加入的商品</p>
          </el-col>
          <el-col :span="12" class="text_align_left margin_top5">
            <el-button class="login_btn">立即登录</el-button>
          </el-col>
        </el-row>

      </div>
      <div>
        <el-table
          ref="multipleTable"
          :data="cartData"
          tooltip-effect="dark"
          style="width: 100%"
          @selection-change="handleSelectionChange">
          <el-table-column
            type="selection"
            width="55">
          </el-table-column>
          <el-table-column label="" width="100">
            <template slot-scope="scope">
              <img :src="scope.row.pic" width="40" height="40" class="head_pic"/>
            </template>
          </el-table-column>
          <el-table-column
            label="商品名称"
            width="120">
            <template slot-scope="scope">{{ scope.row.date }}</template>
          </el-table-column>
          <el-table-column
            prop="name"
            label="单价"
            width="120">
          </el-table-column>
          <el-table-column
            prop="数量"
            label="地址"
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            prop="name"
            label="小计"
            width="120">
          </el-table-column>
          <el-table-column
            fixed="right"
            label="操作"
            width="200">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="deleteOrder(scope.row)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="operate_div">
          <!--indeterminate 属性用以表示 checkbox 的不确定状态，一般用于实现全选的效果(没效果)-->
          <el-row>
            <el-col :span="2" class="margin_top9">
              <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="toggleSelection(cartData)">全选</el-checkbox>
            </el-col>
            <el-col :span="3">
              <el-button type="text" class="regular_font">删除选中商品</el-button>
            </el-col>
            <el-col :span="3">
              <el-button type="text" class="regular_font">清空购物车</el-button>
            </el-col>
            <el-col :span="10" class="text_align_right">
              <p class="regular_font">共<span class="prompt_font">{{goodsQuantity}}</span>件商品 &emsp; &emsp;总共：<span class="prompt_font">¥{{sumMoney}}</span>&emsp; &emsp;已节省:¥{{saveMoney}}</p>
            </el-col>
            <el-col :span="6" class="text_align_right">
              <el-button class="checkout_btn" @click="checkoutEvent()">去结算</el-button>
            </el-col>
          </el-row>

        </div>
      </div>
    </div>
    <!--<home-footer></home-footer>-->
  </div>
</template>

<script>
  import Banner from "../Banner"
  import HomeHeader from "../HomeHeader"
  import HomeFooter from "../HomeFooter"
  import {mapGetters} from 'vuex'
  export default {
    name: "ShoppingCart",
    components: {
      'home-banner': Banner,
      HomeHeader,
      HomeFooter
    },
    data() {
      return{
        loginStatus:'0',
        checkAll:false,
        isIndeterminate:true,
        cartData: [{
          pic:require("@/assets/firm/fenzu_icon_d@2x.png"),
          date: '2016-05-03',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          pic:require("@/assets/firm/fenzu_icon_d@2x.png"),
          date: '2016-05-02',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          pic:require("@/assets/firm/fenzu_icon_d@2x.png"),
          date: '2016-05-04',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          pic:require("@/assets/firm/fenzu_icon_d@2x.png"),
          date: '2016-05-07',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }],
        goodsQuantity:2,
        sumMoney:1500,
        saveMoney:200,
        multipleSelection: []
      }
    },
    computed: {
      ...mapGetters(['cartParams'])
    },
    created() {
      console.log("122=="+this.cartParams);
      this.loginStatus = localStorage.getItem("loginStatus");
    },
    methods: {
      toggleSelection(rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row);
            this.isIndeterminate = false;
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      //去结算
      checkoutEvent(){
        this.$store.dispatch('setCartParams', 'order');
        this.$router.push({ path: '/ConfirmOrder'});
      },
      deleteOrder(row){

      }
    }
  }
</script>

<style scoped>
  @import "../../styles/personal/personal-center.scss";
  .div_padding{
    background-color: white;
    padding-left: 10%;
    padding-right: 10%;
    margin-top: 19px;
  }
  .un_login_prompt {
    border:1px solid #DF2321;
    background-color: #FFECED;
    height: 52px;
  }

  .prompt_font {
    color: #DF2321;
    font-size: 14px;
  }
  .login_btn{
    background-color:#DF2321;
    color: #ffffff;
  }
  .operate_div{
    margin-top: 50px;
    height:46px;
    background:#eeeeee;
  }
  .regular_font {
    color: #666666;
    font-size: 14px;
    /*margin-top: 6px;*/
    /*margin-bottom: 6px;*/
  }
  .checkout_btn{
    background-color:#DF2321;
    color: #ffffff;
    border-radius: 0px;
    width: 120px;
    height:46px;
  }
</style>



