<template>
  <div>
    <!--标题-->
    <el-row>
      <el-col :span="4">
        <div>
          &emsp;
        </div>
      </el-col>
      <el-col :span="8" class="">
        <div class="float_left recommendTitle">
          推荐套餐
        </div>
      </el-col>
      <el-col :span="7">
        <div class="float_right recommendMore">
          查看更多>
        </div>
      </el-col>
      <el-col :span="5">
        <div>
          &emsp;
        </div>
      </el-col>
    </el-row>
    <!--布局-->
    <el-row :gutter="5">
      <el-col :span="4">
        <div>
          &emsp;
        </div>
      </el-col>
      <el-col :span="4" class="">
        <div class="float_left">
          <img src="../assets/checkUp/taocan_banner1@2x.png" height="394" width=""/></div>
      </el-col>
      <el-col :span="11">
        <el-row :gutter="20">
          <el-col :span="8" v-for="(p, index) in productData" :key="index" :class="{'marginTop10':index>2}">
            <el-card :body-style="{ padding: '0px' }" @click="productDetail(p.id)">
              <img src="../assets/checkUp/taocan_img1@2x.png" class="image"  @click="productDetail(p.id)">
              <div class="card_content">
                <p class="card_title">{{p.name}}</p>
                <span class="card_subtitle">{{p.simpleDescription}}</span>
                <p class="margin_top0_bottom0"><span class="discount_price">¥</span><span
                  class="discount_price">{{p.salePrice}}</span><span class="origin_price">起</span>&emsp;&emsp;&emsp;<span
                  class="origin_price line">¥</span><span class="origin_price line">{{p.marketPrice}}</span></p>
              </div>
            </el-card>
          </el-col>
        </el-row>
      </el-col>
      <el-col :span="4">
        <div>
          &emsp;
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  export default {
    name: "HealthCheckup",
    data() {
      return {
        total:0,
        productData:[],
      }
    },
    created() {
      this.getProductList();
    },
    methods: {
      getProductList() {
        // let userId = localStorage.getItem("userId");
        let product = '/api/user/products';
        this.$get(product, {}).then(res => {
          if (JSON.stringify(res.data.code) == 200) {
            this.productData = res.data.data.items;
            console.log("test" + JSON.stringify(res.data.data));
          } else {
            alert(res.data.msg);
          }
        }).catch(function (reason) {
          console.log('catch:', reason);
        });
      },
      productDetail(id){
        debugger
        console.log("111="+id);
        this.$router.push({ path: '/MedicalProductDetail',query:{productId:id}})
      }
    }
  }
</script>

<style scoped>
  @import "../styles/personal/personal-center.scss";

  /*套餐title*/
  .recommendTitle {
    font-size: 22px;
    color: #333333;
  }

  .recommendMore {
    font-size: 14px;
    color: #666666;
  }

  /*套餐的布局*/
  .marginTop10 {
    margin-top: 20px;
  }

  /*.card_content {*/
  /*text-align: left;*/
  /*padding-left: 10px;*/
  /*}*/
  /*.card_title {*/
  /*color: #404040;*/
  /*font-size: 17px;*/
  /*margin-bottom: 0px;*/
  /*margin-top: 10px;*/
  /*}*/
  /*.card_subtitle {*/
  /*font-size: 15px;*/
  /*color: #A2A2A2;*/
  /*}*/
  /*.discount_price{*/
  /*font-size: 25px;*/
  /*color: #DF2321;*/
  /*}*/
  /*.origin_price{*/
  /*font-size: 15px;*/
  /*color: #A2A2A2;*/
  /*}*/
  /*.line{*/
  /*text-decoration:line-through;*/
  /*}*/
  /*.margin_top0_bottom0{*/
  /*margin-top: 0px;*/
  /*margin-bottom: 0px;*/
  /*}*/
  /*.image {*/
  /*width: 100%;*/
  /*display: block;*/
  /*}*/

</style>



